import document from 'global/document';
import React, { Component } from 'react';
import PropTypes from 'prop-types';

import CloseIcon from 'assets/images/common/CloseIcon.svg';

import './Popup.scss';

/**
 * Custom Popup Class
 */
class Popup extends Component {
  // static getDerivedStateFromProps(nextProps, prevState) {
  //   const { isOpen } = nextProps;

  //   if (isOpen !== prevState.isOpen) {
  //     if (!isOpen) {
  //       document.body.scroll = 'yes';
  //       document.body.style.overflowY = 'auto';
  //       nextProps.onClose();
  //     } else {
  //       document.body.scroll = 'no';
  //       document.body.style.overflowY = 'hidden';
  //       nextProps.onOpen();
  //     }
  //     return { isOpen };
  //   }
  //   return null;
  // }

  constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
      closeCallback: null,
    };

    this.openPopup = this.openPopup.bind(this);
    this.closePopup = this.closePopup.bind(this);
    this.setCloseCallBack = this.setCloseCallBack.bind(this);
  }

  componentWillUnmount() {
    document.body.scroll = 'yes';
    document.body.style.overflowY = 'auto';
  }

  setCloseCallBack(callback) {
    this.setState({
      closeCallback: callback,
    });
  }

  togglePopup(flag) {
    this.setState({ isOpen: flag });

    if (!flag) {
      document.body.scroll = 'yes';
      document.body.style.overflowY = 'auto';
      // this.props.onClose();
    } else {
      document.body.scroll = 'no';
      document.body.style.overflowY = 'hidden';
      // this.props.onOpen();
    }
  }

  openPopup(callback = null) {
    this.togglePopup(true);

    if (callback) {
      callback();
    }
  }

  closePopup(callback = null, isFromPop = false) {
    const { closeCallback } = this.state;

    this.togglePopup(false);

    if (callback) {
      callback();
      return;
    }

    if (isFromPop && closeCallback) {
      closeCallback();
    }
  }

  render() {
    const { isOpen } = this.state;
    const { closePopup, setCloseCallBack } = this;
    const { hideCloseButton, style, className } = this.props;

    return (
      <div
        className={
          `popup ${(isOpen) ? 'popup-opened' : 'popup-closed'} ${className}`
        }
      >
        <div className={`cont ${(isOpen) ? 'cont-opened' : 'cont-closed'}`} style={style}>
          {
            hideCloseButton ?
              null
            :
              <div
                role="button"
                onKeyPress={() => {}}
                onClick={() => this.closePopup(null, true)}
                tabIndex={-1}
              >
                <img src={CloseIcon} className="close-icon" alt="close" />
              </div>
          }
          {
            React.Children.map(this.props.children, child => (
              React.cloneElement(child, {
                closePopUp: closePopup,
                setCloseCallBack,
              })
            ))
          }
        </div>
      </div>
    );
  }
}

Popup.propTypes = {
  children: PropTypes.node,
  // isOpen: PropTypes.bool, // eslint-disable-line
  // onClose: PropTypes.func,
  // onOpen: PropTypes.func,
  hideCloseButton: PropTypes.bool,
  style: PropTypes.object,
  className: PropTypes.string,
};

Popup.defaultProps = {
  children: null,
  // isOpen: false,
  // onClose: () => {},
  // onOpen: () => {},
  hideCloseButton: false,
  style: {},
  className: '',
};

export default Popup;
